html,
body {
  height: 100%;
  background-color: rgb(46, 48, 71);
}

.wrapper {
  min-height: 100%;

  /* flex */
  display: flex;
  flex-direction: column;

  /* grid
  display: grid;
  grid-template-rows: auto 1fr auto; */
}

.head,
.main,
.footer {
  text-align: center;
  padding: 15px;
  color: #fff;
  font-size: 24px;
  letter-spacing: 2px;
}

.head {
  background-color: rgba(61, 61, 219);
}

.main {
  flex: 1 1 auto;
}

.footer {
  background-color: red;
}

.gameZone {
  width: 516px;
  display: flex;
  flex-wrap: wrap;
  margin: auto;
}

.brick {
  width: 50px;
  height: 50px;
  border: 1px solid black;
  background-color: aqua;
}

.brick2 {
  width: 51.6px;
  height: 31.6px;
}
